import "./App.css";
import Login from "./component/screens/Login";
import Profiles from "./component/screens/Profiles";
import Signup from "./component/screens/Signup";
import ChatScreen from "./component/screens/ChatScreen";
import { BrowserRouter, Routes, Route } from "react-router-dom";
import PrivateRoute from "../src/component/screens/PrivateRoute";
import { AuthProvider } from "./context/AuthContext";

function App() {
  

  return (
    <>
      <BrowserRouter>
        <AuthProvider>
          <Routes>
            <Route path="/login/" element={<Login />} />
            <Route path="/signup" element={<Signup />} />
            <Route path="/" element={<PrivateRoute />}>
              <Route index element={<Profiles />} />
              <Route path="/chat/:oponent_id/" element={<ChatScreen />} />
            </Route>
          </Routes>
        </AuthProvider>
      </BrowserRouter>
    </>
  );
}

export default App;
