import { jwtDecode } from "jwt-decode";
import { createContext, useState, useEffect } from "react";
import { useNavigate, useLocation } from "react-router-dom";
import { BASE_URL } from "../key";

const AuthContext = createContext();

export default AuthContext;

export const AuthProvider = ({ children }) => {
  const navigate = useNavigate();

  const [authToken, setAuthToken] = useState(() =>
    localStorage.getItem("authTokens")
      ? JSON.parse(localStorage.getItem("authTokens"))
      : null
  );

  

  const [user, setUser] = useState(() =>
    localStorage.getItem("authTokens")
      ? jwtDecode(localStorage.getItem("authTokens"))
      : null
  );

  const [loading, setLoading] = useState(true);

  // Login
  let loginUser = async (e) => {
    e.preventDefault();

    console.log("Form Submitted");
    let response = await fetch(`${BASE_URL}/api/v1/auth/token/`, {
      method: "POST",
      headers: {
        "Content-Type": "application/json",
      },
      body: JSON.stringify({
        username: e.target.username.value,
        password: e.target.password.value,
      }),
    });

    let data = await response.json();
    console.log("this is my token", data);

    if (response.status == 200) {
      setAuthToken(data);
      localStorage.setItem("authTokens", JSON.stringify(data));
      console.log(data);
      const decodedUser = jwtDecode(data.access);
      setUser(decodedUser);

      navigate("/");
    }
  };

  // Signup

  let registerUser = async (e) => {
    e.preventDefault();

    console.log(e.target.email.value);
    let response = await fetch(`${BASE_URL}/api/v1/auth/create/`, {
      method: "POST",
      headers: {
        "Content-Type": "application/json",
      },
      body: JSON.stringify({
        email: e.target.email.value,
        password: e.target.password.value,
        name: e.target.first_name.value,
      }),
    });

    let data = await response.json();
    console.log("this is my token", data);

    if (response.status == 200) {
      setAuthToken(data.data);
      localStorage.setItem("authTokens", JSON.stringify(data.data));
      console.log(data.data);
      const decodedUser = jwtDecode(data.data.access);
      setUser(decodedUser);

      navigate("/");
    }
  };

  const logut = () => {
    setAuthToken(null);
    setUser(null);
    localStorage.removeItem("authTokens");
    navigate("/login");
  };

  // let updateToken = async () => {
  //   console.log("update token called");
  //   let response = await fetch(
  //     "http://127.0.0.1:8000/api/v1/auth/token/refresh/",
  //     {
  //       method: "POST",
  //       headers: {
  //         "Content-Type": "application/json",
  //       },
  //       body: JSON.stringify({
  //         refresh: authToken?.refresh,
  //       }),
  //     }
  //   );

  //   let data = await response.json();

  //   if (response.status == 200) {
  //     setAuthToken(data);
  //     localStorage.setItem("authTokens", JSON.stringify(data));
  //     console.log('updated');
  //     setUser(jwtDecode(data.access));
  //   } else {
  //     logut();
  //   }

  //   if (loading) {
  //     setLoading(false);
  //   }
  // };

  let contextData = {
    user: user,
    authToken: authToken,
    setAuthToken: setAuthToken,
    registerUser: registerUser,
    setUser: setUser,
    logut: logut,
    loginUser: loginUser,
  };

  useEffect(() => {
    if (authToken) {
      setUser(jwtDecode(authToken.access));
    }
    setLoading(false);
    // if (loading) {
    //   updateToken();
    // }
    // let fiveminuts = 1000 * 60 * 4;
    // let intervel = setInterval(() => {
    //   if (authToken) {
    //     updateToken();
    //   }
    // }, fiveminuts);
    // return () => clearInterval(intervel);
  }, [authToken, loading]);

  return (
    <AuthContext.Provider value={contextData}>
      {loading ? null : children}
    </AuthContext.Provider>
  );
};
