import { useContext } from "react";
import styled from "styled-components";
import AuthContext from "../../context/AuthContext";
import { useNavigate } from "react-router-dom";

const Login = () => {
  let { loginUser } = useContext(AuthContext);

  let navigate = useNavigate();
  return (
    <Wrapper>
      <ChiledWrapper>
        <Hedding>Login</Hedding>
        <SubHedding>Sign in to continue</SubHedding>
        <Form onSubmit={loginUser}>
          <InputTitile>NAME</InputTitile>
          <Input type="text" name="username" id="" />
          <InputTitile>PASSWORD</InputTitile>
          <Input type="password" name="password" id="" />
          <h1
            style={{
              color: "#fff",
              fontSize: "15px",
              textAlign: "end",
              marginTop: "15px",
            }}
            onClick={() => navigate("/signup")}
          >
            signup
          </h1>
          <SubmitButton onSubmit={loginUser}>Login</SubmitButton>
        </Form>
      </ChiledWrapper>
    </Wrapper>
  );
};

const Wrapper = styled.div`
  min-height: 100vh;
  background-color: black;
  display: flex;
  justify-content: center;
  align-items: center;
`;
const ChiledWrapper = styled.div`
  width: 25%;
`;
const Hedding = styled.h1`
  text-align: center;
  color: white;
  font-size: 48px;
`;
const SubHedding = styled.p`
  text-align: center;
  color: white;
  font-size: 22px;
`;
const Form = styled.form``;
const InputTitile = styled.p`
  font-size: 16px;
  color: white;
  margin-top: 20px;
  margin-bottom: 5px;
`;
const Input = styled.input`
  width: 100%;
  border: 1px solid white;
  background-color: black;
  height: 40px;
  color: white;
  padding-left: 10px;
  font-size: 14px;
  &:focus {
    outline: none;
  }
`;
const SubmitButton = styled.button`
  background-color: white;
  margin: 20px 14%;
  border: 0px;
  width: 75%;
  height: 40px;
`;

export default Login;
