import axios from "axios";
import { jwtDecode } from "jwt-decode";
import dayjs from "dayjs";
import { BASE_URL } from "../../key";

// const baseURL = "http://127.0.0.1:8000";


let authToken = localStorage.getItem("authTokens")
  ? JSON.parse(localStorage.getItem("authTokens"))
  : null;

console.log(authToken);

const axiosInstence = axios.create({
  BASE_URL,
  headers: { Authorization: `Bearer ${authToken?.access}` },
});

axiosInstence.interceptors.request.use(async (req) => {
  if (!authToken) {
    authToken = localStorage.getItem("authTokens")
      ? JSON.parse(localStorage.getItem("authTokens"))
      : null;
    req.headers.Authorization = `Bearer ${authToken?.access}`;
  }

  const user = jwtDecode(authToken.access);
  const isExpired = dayjs.unix(user.exp).diff(dayjs()) < 1;
  console.log("is Expired ", isExpired);
  if (!isExpired) return req;

  const response = await axios.post(`${BASE_URL}/api/v1/auth/token/refresh/`, {
    refresh: authToken.refresh,
  });

  localStorage.setItem("authTokens", JSON.stringify(response.data));
  req.headers.Authorization = `Bearer ${response.data.access}`;
  return req;
});

export default axiosInstence;
