import React, { useState, useEffect } from "react";
import styled from "styled-components";
import { useNavigate } from "react-router-dom";

import useAxios from "./useAxios";
import { useContext } from "react";
import AuthContext from "../../context/AuthContext";

const Profiles = () => {
  let api = useAxios();
  const navigate = useNavigate();

  const [datas, setDatas] = useState([]);
  const [loading, setloading] = useState(false);
  let { logut } = useContext(AuthContext);

  useEffect(() => {
    intiolyLod();
  }, []);

  const intiolyLod = async () => {
    let response = await api.get("/api/v1/main/");

    if (response.status == 200) {
      setDatas(response.data.data);
    } else {
      setloading(true);
    }
  };

  const HandleClick = (oponent_id, user) => {
    console.log(user);
    navigate(`/chat/${oponent_id}`, {
      state: {
        user: user,
      },
    });
  };

  return (
    <Wrapper>
      <CiledWrapper>
        {loading && <h1>Loading...</h1>}
        <h1
          style={{
            color: "#fff",
            fontSize: "15px",
            textAlign: "left",
            marginTop: "15px",
          }}
          onClick={logut}
        >
          logout
        </h1>
        <ListPerarent>
          {datas.map((data) => {
            return (
              <List key={data.id} onClick={() => HandleClick(data.id, data)}>
                {data.profileimage ? (
                  <Left>
                    <img src={data.profileimage} />
                  </Left>
                ) : (
                  <Left>
                    <img src={require("../Assets/wmpty.png")} />
                  </Left>
                )}
                <Name>{data.user}</Name>
              </List>
            );
          })}
        </ListPerarent>
      </CiledWrapper>
    </Wrapper>
  );
};

const Wrapper = styled.div`
  background-color: black;
  min-height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
`;
const CiledWrapper = styled.div`
  width: 35%;
`;
const ListPerarent = styled.ul`
  width: 100%;
`;
const List = styled.li`
  background-color: white;
  margin-top: 10px;
  height: 70px;
  border-radius: 50px;
  display: flex;
`;
const Left = styled.div`
  margin-top: 10px;
  margin-left: 10px;
  width: 50px;

  height: 50px;
  border-radius: 50px;
`;
const Name = styled.h3`
  width: calc(100% - 100px);
  color: #000000;
  display: flex;
  /* justify-content: center; */
  margin-left: 10px;
  align-items: center;
`;

export default Profiles;
