import axios from "axios";
import { jwtDecode } from "jwt-decode";
import dayjs from "dayjs";
import { useContext } from "react";
import axiosInstence from "./axiosInstence";
import AuthContext from "../../context/AuthContext";

const baseURL = "https://chatapppy.pythonanywhere.com";

const useAxios = () => {
  const { authToken, setUser, setAuthToken } = useContext(AuthContext);

  const axiosInstence = axios.create({
    baseURL,
    headers: { Authorization: `Bearer ${authToken?.access}` },
  });

  axiosInstence.interceptors.request.use(async (req) => {
    const user = jwtDecode(authToken.access);
    const isExpired = dayjs.unix(user.exp).diff(dayjs()) < 1;

    if (!isExpired) return req;

    const response = await axios.post(`${baseURL}/api/v1/auth/token/refresh/`, {
      refresh: authToken.refresh,
    });

    localStorage.setItem("authTokens", JSON.stringify(response.data));

    setAuthToken(response.data);
    setUser(jwtDecode(response.data.access));

    req.headers.Authorization = `Bearer ${response.data.access}`;
    return req;
  });

  return axiosInstence;
};

export default useAxios;
