import React from "react";
import styled from "styled-components";
import { useState, useEffect, useContext } from "react";
import { useLocation, useParams } from "react-router-dom";
import AuthContext from "../../context/AuthContext";
import useAxios from "./useAxios";
import { jwtDecode } from "jwt-decode";

const ChatScreen = () => {
  const [input, setInput] = useState("");
  const [userchat, setUserchat] = useState([]);
  const [userdetails, setuserdetails] = useState([]);

  const [data, setData] = useState([]);
  const [allresponse, setAllresponse] = useState([]);
  const [oponentchat, setOponentchat] = useState([]);
  const [allchat, setallchat] = useState([]);

  const { authToken, logut } = useContext(AuthContext);
  let api = useAxios();
  const location = useLocation();
  let user_id = jwtDecode(authToken.access);
  let { oponent_id } = useParams();

  useEffect(() => {
    const { user } = location.state;
    console.log(user);
    setuserdetails(user);
  }, []);

  const HnadleSubmit = (e) => {
    e.preventDefault();
    if (input.length > 0) {
      api
        .post(
          `/api/v1/main/comment/${oponent_id}/`,
          {
            comment: input,
          },
          {
            headers: {
              Authorization: `Bearer ${authToken.access}`,
            },
          }
        )
        .then((response) => {
          if (response.status == 200) {
            setAllresponse(response.data);
          } else if (response.statusText == "Unauthorized") {
            console.log("unotorized");
            logut();
          }
        });

      setUserchat(input);
      setInput("");
    }
  };

  useEffect(() => {
    api
      .get(`/api/v1/main/chatslist/${oponent_id}/`, {
        headers: {
          Authorization: `Bearer ${authToken.access}`,
        },
      })
      .then((response) => {
        if (response.status == 200) {
          setData(response.data.data);
        } else if (response.statusText == "Unauthorized") {
          console.log("unotorized");
          logut();
        }
      });
  }, [allresponse]);

  useEffect(() => {
    const fetchData = async () => {
      try {
        const response = await api.get(
          `/api/v1/main/oponentchat/${oponent_id}/`,
          {
            headers: {
              Authorization: `Bearer ${authToken.access}`,
            },
          }
        );
        if (response.status == 200) {
          setOponentchat(response.data.data);
        } else if (response.statusText == "Unauthorized") {
          console.log("unotorized");
          logut();
        }
      } catch (error) {
        console.error("Error fetching data:", error);
      }
    };
    const fetchDataInterval = setInterval(() => {
      fetchData();
    }, 1000);

    return () => clearInterval(fetchDataInterval);
  }, [oponent_id, authToken]);

  useEffect(() => {
    const handleChat = () => {
      let newdata = null;

      if (oponentchat && !data) {
        newdata = [...oponentchat];
      } else if (!oponentchat && data) {
        newdata = [...data];
      } else if (oponentchat && data) {
        newdata = [...data, ...oponentchat];
      }
      newdata.sort((a, b) => a.id - b.id);
      // data
      setallchat(newdata);
    };

    handleChat();

    const intervalId = setInterval(() => {
      handleChat();
    }, 1000);

    return () => clearInterval(intervalId);
  }, [oponentchat, data, setallchat, allresponse]);

  const chatContent = () => {
    return (
      <ChateSection>
        <OpponentChat>
          {allchat &&
            allchat.map((item, index) => (
              <h3
                className={user_id.user_id == item.user_id ? "oponent" : "user"}
                key={index}
              >
                {item.comment}
              </h3>
            ))}
        </OpponentChat>
      </ChateSection>
    );
  };

  return (
    <Wrapper>
      <Main>
        <MainListParent>
          <HeddSection>
            <HedContainer>
              <HedLeft>
                <LeftDp>
                  {userdetails.profileimage ? (
                    <img src={userdetails.profileimage} alt="" />
                  ) : (
                    <img src={require("../Assets/wmpty.png")} alt="" />
                  )}
                </LeftDp>
              </HedLeft>
              <HedRight>
                <Name>{userdetails.user}</Name>
              </HedRight>
            </HedContainer>
          </HeddSection>
          <Middle>{chatContent()}</Middle>
          <BottomSection>
            <FormContainer action="" onSubmit={(e) => HnadleSubmit(e)}>
              <Input
                type="text"
                placeholder="Enter Youer Message"
                onChange={(e) => setInput(e.target.value)}
                value={input}
              />

              <SendArrow
                src={require("../Assets/send.png")}
                alt=""
                onClick={(e) => HnadleSubmit(e)}
              />
            </FormContainer>
          </BottomSection>
        </MainListParent>
      </Main>
    </Wrapper>
  );
};

// Hedd Section
const Wrapper = styled.div`
  min-height: 100vh;
  background-color: black;
`;
const Main = styled.div`
  padding: 50px 80px;
`;
const MainListParent = styled.ul`
  width: 100%;
`;
const HeddSection = styled.li`
  width: 100%;
  height: 60px;
  background-color: #b2ffab;
  border-radius: 50px;
`;
const HedContainer = styled.ul`
  display: flex;
  margin-left: 10px;
`;
const HedLeft = styled.li``;
const LeftDp = styled.div`
  width: 50px;
  height: 50px;

  border-radius: 50px;
  margin-top: 5px;
`;
const HedRight = styled.div`
  display: flex;
  justify-content: center;
  width: calc(100% - 100px);
  align-items: center;
`;
const Name = styled.h1``;

// Middle Section
const Middle = styled.li`
  height: 70vh;
  overflow-y: scroll;
  -ms-overflow-style: none;
  scrollbar-width: none;
  &::-webkit-scrollbar {
    display: none;
  }
`;
const ChateSection = styled.div`
  margin-top: 50px;
`;
const OpponentChat = styled.div``;
const Opponent = styled.h3`
  display: flex;
  justify-content: start;
  margin-top: 20px;
  background-color: #7ed957;
  color: white;
  width: fit-content;
  padding: 10px 20px;
  border-radius: 10px;

  border-top-left-radius: 0px;
`;


// Bottom Section
const BottomSection = styled.li``;
const FormContainer = styled.form``;
const Input = styled.input`
  font-size: 16px;
  font-weight: 600;
  border: 5px solid #b2ffab;
  background-color: #efefef;
  padding-left: 20px;
  outline: none;
  width: calc(100% - 20px);
  height: 50px;
  border-radius: 50px;
`;
const SendArrow = styled.img`
  width: 40px;
  position: relative;
  top: -40px;
  float: right;
  padding-right: 20px;
`;

export default ChatScreen;
